import React, { useState } from 'react';
import styles from './pagination.module.css';
import classNames from 'classnames';

export default function Pagination({ totalItemsCount, pageSize, currentPage, onPageChanged, portionSize}) {

  let pagesCount = Math.ceil(totalItemsCount / pageSize);

  let pages = []
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }

  const portionsCount = Math.ceil(pagesCount / portionSize);

  const [portionNumber, setPortionNumber] = useState(+localStorage.getItem('num') || 1);

  const leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
  const rightPortionPageNumber = portionNumber * portionSize;

  const onLeftStep = () => {
    console.log(portionNumber-1);
    setPortionNumber(portionNumber - 1);
    localStorage.setItem('num', portionNumber - 1 );
  }

  const onRightStep = () => {
    console.log(portionNumber+1);
    setPortionNumber(portionNumber + 1);
    localStorage.setItem('num', portionNumber + 1);
  }

  const onLeftOver = () => {
    setPortionNumber(1);
    localStorage.setItem('num', 1);
  }

  const onRightOver = () => {
    setPortionNumber(portionsCount);
    localStorage.setItem('num', portionsCount);
  }

  return (
    <div className={styles.paginationControls}>
      <button
        disabled={portionNumber === 1}
        onClick={onLeftOver}
        className={classNames(styles.togglePageBtn, styles.pageBtn, styles.toggleLeftOver)}
      ></button>
      <button
        disabled={portionNumber === 1}
        onClick={onLeftStep}
        className={classNames(styles.togglePageBtn, styles.pageBtn, styles.toggleLeftStep)}
      ></button>

      {pages
        .filter(page => page >= leftPortionPageNumber && page <= rightPortionPageNumber)
        .map((page) => {
          return <button
            className={page === currentPage
              ? classNames(styles.activePage, styles.pageBtn)
              : styles.pageBtn}
            id={page}
            onClick={() => { onPageChanged(page) }}
          >
            {page}
          </button>
        })}

      <button
        disabled={portionNumber === portionsCount}
        onClick={onRightStep}
        className={classNames(styles.togglePageBtn, styles.pageBtn, styles.toggleRightStep)}
      ></button>
      <button
        disabled={portionNumber === portionsCount}
        onClick={onRightOver}
        className={classNames(styles.togglePageBtn, styles.pageBtn, styles.toggleRightOver)}
      ></button>

      <span className={styles.currentPage}>current: {currentPage}</span>
    </div>
  )
}
