import React from 'react';
// import { connect} from 'react-redux';
import { follow, getUsers, unfollow } from '../../../redux/usersReducer';
import Users from './Users';
import Preloader from '../../common/Preloder/Preloader';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { selectCurrentPage, selectFollowingProgress, selectIsFetching, selectPageSize, selectTotalUsersCount, selectUsers } from '../../../redux/usersSelectors';

export default function UsersContainer(props) {

  let dispatch = useDispatch();

  const users = useSelector(selectUsers);
  const pageSize = useSelector(selectPageSize);
  const totalUsersCount = useSelector(selectTotalUsersCount);
  const currentPage = useSelector(selectCurrentPage);
  const isFetching = useSelector(selectIsFetching);
  const followingProgress = useSelector(selectFollowingProgress);

  useEffect(() => {
    dispatch(getUsers(currentPage, pageSize));
  }, [dispatch, currentPage, pageSize]);

  let onPageChanged = (pageNumber) => {
    dispatch(getUsers(pageNumber, pageSize));
  }

  let onClickFollow = (userId) => {
    dispatch(follow(userId));
  }

  let onClickUnfollow = (userId) => {
    dispatch(unfollow(userId));
  }

  return (
    <>
      {isFetching
        ? <Preloader />
        : null
      }
      <Users
        // to UserItem
        users={users}
        isfething={isFetching}
        followingProgress={followingProgress}
        onClickFollow={onClickFollow}
        onClickUnfollow={onClickUnfollow}
        // to Controls
        onPageChanged={onPageChanged}
        pageSize={pageSize}
        totalUsersCount={totalUsersCount}
        currentPage={currentPage}
      />
    </>
  )
}

