import React from 'react';
import styles from './useritem.module.css';
import avatarDefault from '../../../../assets/images/defaultAvatar.png'
import { NavLink } from 'react-router-dom';
import classNames from 'classnames';

export default function UserItem(props) {

  let onFollow = () => {
    if (props.followed) {
      props.onClickUnfollow(props.id)
    } else {
      props.onClickFollow(props.id)
    }
  }

  return (
    <div className={styles.userItem}>
      <div className={styles.userControl}>
        <div className={styles.avatarBox}>
          <NavLink to={'/profile/' + props.id}>
            <img src={props.imgSrc.small ? props.imgSrc.small : avatarDefault} alt="avatar" />
          </NavLink>
        </div>
        <button className={props.followed
          ? classNames(styles.followBtn, styles.unFollowed)
          : classNames(styles.followBtn, styles.followed)}

          disabled={props.followingProgress.some((id) => props.id === id)}

          onClick={onFollow}
        >
          {props.followed ? 'Unfollow' : 'Follow'}
        </button>
      </div>
      <div className={styles.userInfo}>
        <div className={styles.aboutUser}>
          <span className={styles.fullName}>
            {props.name}
          </span>
          <span className={styles.status}>
            {props.status ? props.status : 'Here will be user\'s status'}
          </span>
        </div>
        <div className={styles.location}>
          <span>{props.location ? props.location.country : 'Country'},
            <br />
            {props.location ? props.location.city : 'City'}</span>
        </div>
      </div>
    </div>
  )
}

